import React, { useState } from "react";
import arrow from "../icons/arrow.png";
import css from "./rsvp.less";

const RSVP = () => {
  const [code, setCode] = useState("");

  const onChange = e => {
    const newCode = e.target.value;
    if (newCode.length <= 5) {
      setCode(newCode);
    }
  };

  return (
    <div className={css.container}>
      <div className={css.upsell}>Gi beskjed her</div>
      <img src={arrow} alt="" className={css.arrow} />
      <div className={css.header}>RSVP</div>
      <div className={css.description}>
        <div>
          Håper at du har anledning til å være med på feiringen av dagen vår
        </div>
        <div>
          Fortell oss om du kan komme eller ikke ved å taste inn din unike kode
          fra innbydelsen
        </div>
      </div>
      <input
        value={code}
        onChange={onChange}
        className={css.inputField}
        placerholder="Skriv her"
      />
    </div>
  );
};

export default RSVP;
